            :type="'number'"
            :message="message"
            @input="message = ''"
            :required="true"
            v-model="data.inn"
          />
        </div>
        <div class="requisites-fields-row fields-row">
          <base-input
            :id="'requisitesAddress'"
            :label="'Адрес'"
            :required="true"
            v-model="data.address_street"
          />
        </div>
        <div class="requisites-fields-row fields-row">
          <base-input
            :id="'requisitesCity'"
            :label="'Город'"
            v-model="data.address_city"
          />
        </div>
        <div class="requisites-fields-row fields-row">
          <base-input
            :id="'requisitesCountry'"
            :label="'Страна'"
            :required="true"
            v-model="data.address_country"
          />
        </div>
        <div class="requisites-fields-row fields-row _index">
          <base-input
            :id="'requisitesIndex'"
            :label="'Индекс'"
            :type="'number'"
