          Сохранить
        </base-button>
      </div>
    </div>
    <!-- search -->
    <div class="requisites-search page-wrapper">
      <div class="requisites-search-field">
        <base-input
          :id="'requisitesSearch'"
          :placeholder="!$isMobile ? 'Поиск по контрагенту или ИНН' : 'Поиск'"
          :search="true"
          :searchIcon="true"
          :suggestionWidth="380"
          :suggestions="suggestions"
          @setSuggestion="getCompanies('', $event)"
          @keypressEnter="getCompanies('')"
          @clear="(query = ''), (suggestions = []), getCompanies()"
          @input="getCompanies('search')"
          v-model="query"
        />
      </div>
    </div>
    <!-- table -->
    <div class="page-wrapper" v-if="!edit">
      <company-requisites-table :companies="companies" @edit="editCompany" />
    </div>
  </section>
  <base-modal
    :open="openModal"
    :text="'Отправлено'"
    @close="openModal = false"
  />
</template>

<script>
import { useMeta } from "vue-meta";
import CompanyRequisitesTable from "@/components/companyRequisites/CompanyRequisitesTable.vue";

export default {
  name: "CompanyRequisites",
  components: {
    CompanyRequisitesTable,
  },
  computed: {
    user() {
      return this.$store.getters.user;
    },
  },
  data() {
    return {
      data: {
        name: "",
        inn: "",
        address_street: "",
        address_city: "",
        address_country: "",
        address_index: "",
        phone_number: "",
        email: "",
        director_full_name: "",
        director_position: "",
        logo: null,
        blank_photo: null,
      },
      message: "",
      query: "",
      openModal: false,
      edit: false,
      id: "",
      companies: [],
      suggestions: [],
    };
  },
  setup() {
    useMeta({ title: "Реквизиты компании" });
  },
  mounted() {
    this.getCompanies();
  },
  methods: {
    getCompanies(type = "", id = "") {
      !type && this.$store.dispatch("loading", true);
      const params = {
        [this.query && "search"]: type === "search" ? this.query : id,
      };
      this.suggestions = [];
      this.$api
        .get(`${this.$api.defaults.API}/company`, { params })
        .then((res) => {
          if (type !== "search") this.companies = [];
          for (let c of res.data.results) {
            if (type !== "search") this.companies.push(c);
            if (type == "search") {
              // get search suggestions
              if (this.query) {
                this.suggestions.push({
                  key: c.name,
                  value: c.id,
                });
              }
            }
          }
          this.$store.dispatch("loading", false);
        })
        .catch((err) => {
          new Error(err);
          this.$store.dispatch("loading", false);
        });
    },
    addCompany() {
      if (!Object.values(this.data).some((i) => !i)) {
        this.$store.dispatch("loading", true);
        // edit
        if (this.edit) {
          this.$api
            .patch(`${this.$api.defaults.API}/company/${this.id}`, this.data)
            .then((res) => {
              const index = this.companies.findIndex(
                (i) => i.id == res.data.id
              );
              this.companies[index] = res.data;
              for (const [key] of Object.entries(this.data)) {
                this.data[key] = "";
              }
              this.$store.dispatch("loading", false);
              this.edit = false;
              this.openModal = true;
            })
            .catch((err) => {
              new Error(err);
              this.$store.dispatch("loading", false);
            });
        }
        // add
        else {
          this.$api
            .post(`${this.$api.defaults.API}/company`, this.data)
            .then((res) => {
              this.companies.push(res.data);
              for (const [key] of Object.entries(this.data)) {
                this.data[key] = "";
              }
              this.$store.dispatch("loading", false);
              this.openModal = true;
            })
            .catch((err) => {
              new Error(err);
              this.$store.dispatch("loading", false);
              if (err.response.data.inn)
                this.message = "Данный ИНН уже существует в нашей базе данных";
            });
        }
      }
    },
    uploadImage(type, field) {
      const file = this.$refs[field].files[0];
      const reader = new FileReader();
      reader.readAsDataURL(file);
      reader.onload = () => {
        this.data[type] = reader.result;
      };
    },
    drop(type, field) {
      const e = window.event;
      e.preventDefault();
      this.$refs[field].files = e.dataTransfer.files;
      this.uploadImage(type, field);
    },
    editCompany(val) {
      const company = this.companies.find((i) => i.id === val);
      this.id = company.id;
      this.edit = true;
      for (const [key] of Object.entries(company)) {
        this.data[key] = company[key];
      }
    },
  },
};
</script>

